
.navbar{
    height: 5rem;
    width: 100%;
    margin: 0 2rem;
    display: flex;
    align-items: center;
    padding: 1rem 2rem;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
    background: rgb(30, 30, 30);
}
.logo{
    object-fit: cover;
    width: 60px;
    border-radius: 50%;
}
.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
    font-size: 1.5rem;
}
.desktopMenuListItem:hover{
    color: rgb(145, 255, 0);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.desktopMenuBtn{
    background: #fff;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    margin-right: 3rem;
}
.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem 1rem 1rem 0;
}
.active{
   color: yellow; 
   padding-bottom: 0.5rem;
   border-bottom: 3px solid yellow;
}
.mobMenu{
    display: none;
    font-size: 4rem;
    
}
.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}

.ListItem{
    color: #fff;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    border-radius: 4px;
    background-color: rgb(30, 30, 30);
}
@media screen and (max-width:720px) {
    .mobMenu{
        display: flex;
    }
    .desktopMenuListItem{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
}
